.App {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: space-between;
  height: 100vh;
  color: white;
  text-align: center;
  background-color: #272727;
  font-family: 'Lato', sans-serif; }
  .App * {
    font-family: 'Lato', sans-serif; }
  .App .App_content {
    flex: 1 1;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    overflow: hidden; }
    .App .App_content > * {
      flex: 1 1; }
  .App .Loader {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%); }

.Loader {
  display: flex; }
  .Loader .Loader_dot {
    display: block;
    width: 0.8em;
    height: 0.8em;
    margin-right: 0.8em;
    background-color: black;
    border: 3px solid white;
    -webkit-animation-name: bounce;
            animation-name: bounce;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out; }

@-webkit-keyframes bounce {
  from {
    -webkit-transform: translateY(0px) scale(0.7, 1.1);
            transform: translateY(0px) scale(0.7, 1.1); }
  30% {
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in; }
  50% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px); }
  55% {
    -webkit-transform: translateY(5px) scale(1.3, 0.7);
            transform: translateY(5px) scale(1.3, 0.7); }
  65% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px); }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px); } }

@keyframes bounce {
  from {
    -webkit-transform: translateY(0px) scale(0.7, 1.1);
            transform: translateY(0px) scale(0.7, 1.1); }
  30% {
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in; }
  50% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px); }
  55% {
    -webkit-transform: translateY(5px) scale(1.3, 0.7);
            transform: translateY(5px) scale(1.3, 0.7); }
  65% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px); }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px); } }

.Header {
  flex: 0 0 auto;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.2em 10vw;
  background-color: #169cb4;
  color: #ffdd55;
  width: 100%;
  box-shadow: 0px -20px 16px 20px rgba(0, 0, 0, 0.25); }
  .Header h1 {
    margin: 0.4em 0.8em;
    font-size: 1em;
    text-align: left; }
  .Header .Header_logo {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0.3em 0.1em;
    width: 2.7em;
    height: 2.7em; }
    .Header .Header_logo span {
      position: absolute;
      display: block;
      width: 100%;
      height: 100%;
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
      -webkit-transform-origin: center;
              transform-origin: center;
      -webkit-transform: scale(0.9);
              transform: scale(0.9);
      -webkit-transition: -webkit-transform 0.1s ease-out;
      transition: -webkit-transform 0.1s ease-out;
      transition: transform 0.1s ease-out;
      transition: transform 0.1s ease-out, -webkit-transform 0.1s ease-out;
      -webkit-filter: hue-rotate(180deg) contrast(0) brightness(100);
              filter: hue-rotate(180deg) contrast(0) brightness(100); }
    .Header .Header_logo img {
      width: 100%;
      z-index: 10;
      -webkit-transition: -webkit-transform 0.1s ease-out, -webkit-filter 0.1s ease-out;
      transition: -webkit-transform 0.1s ease-out, -webkit-filter 0.1s ease-out;
      transition: transform 0.1s ease-out, filter 0.1s ease-out;
      transition: transform 0.1s ease-out, filter 0.1s ease-out, -webkit-transform 0.1s ease-out, -webkit-filter 0.1s ease-out; }
    .Header .Header_logo:hover span {
      -webkit-transform: scale(1.2) translate(-0.06em, -0.02em);
              transform: scale(1.2) translate(-0.06em, -0.02em); }
    .Header .Header_logo:hover img {
      -webkit-filter: brightness(1.2);
              filter: brightness(1.2); }
  .Header .Header_title {
    margin-left: 0.8em;
    height: 1.3em; }

.HeaderNavigation {
  display: block;
  height: auto; }
  .HeaderNavigation a {
    margin-left: 0.5em;
