.PlayerCounter {
  margin: 100px auto;
  position: relative;
  width: 300px;
  padding: 2em;
  color: #9a1616; }
  .PlayerCounter::before {
    content: "";
    position: absolute;
    top: -4px;
    right: -4px;
    bottom: -4px;
    left: -4px;
    display: block;
    background-color: #eb5626;
    -webkit-transform: rotate(2deg);
            transform: rotate(2deg);
    z-index: -1; }
  .PlayerCounter::after {
    content: "";
    position: absolute;
    top: -4px;
    right: -4px;
    bottom: -4px;
    left: -4px;
    display: block;
    background-color: #ffdd55;
    -webkit-transform: rotate(-2deg);
            transform: rotate(-2deg);
    z-index: -1; }
  .PlayerCounter p {
    margin: 0;
    font-size: 1.2em; }
  .PlayerCounter p.count {
    margin: 0.1em 0 0.7em 0;
    font-size: 2em;
    font-weight: bold; }

